import React, { useState } from "react";
import { createPortal } from "react-dom";
import { useLocation } from "react-router-dom";
import {NavSvg, NavPng} from './NavSvg'
import { ASIDELINK } from "../..";


export default function LinkButton({ name }) {
    const location = useLocation();
    const isActive = location.pathname === `${ASIDELINK}/${name}`;
    const [tooltip, setTooltip] = useState({ visible: false, x: 0, y: 0 });
    const [timeoutId, setTimeoutId] = useState(null);

    const handleMouseEnter = (e) => {
        const rect = e.currentTarget.getBoundingClientRect();
        const id = setTimeout(() => {
        setTooltip({
            visible: true,
            x: rect.right + 10, //   
            y: rect.top + rect.height / 5.5, //   
        });
        }, 500); //   0.5 
        setTimeoutId(id);
    };

    const handleMouseLeave = () => {
        clearTimeout(timeoutId); //  ,      
        setTooltip((prev) => ({ ...prev, visible: false })); //  
    };

    return (
        <div
        className="relative group"
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        >
        <a
            href={`${ASIDELINK}/${name}`}
            className={`items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 ${
            isActive ? "bg-neutral-400 text-white" : "text-primary-foreground"
            } h-10 flex p-0`}
        >
            <button
            className="items-center justify-center whitespace-nowrap rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 h-10 flex p-0"
            data-state="closed"
            >
                <NavPng state={name} />
            </button>
        </a>
        <Tooltip x={tooltip.x} y={tooltip.y} visible={tooltip.visible}>
            <TooltipDesc name={name} />
        </Tooltip>
        </div>
    );
}

function Tooltip({ children, x, y, visible }) {
    return createPortal(
        <div
        style={{
            position: "absolute",
            top: y,
            left: x,
            zIndex: 1050,
            backgroundColor: "white", //  
            color: "black", //  
            padding: "5px 10px", //  
            borderRadius: "5px", //   
            fontSize: "14px", //   
            whiteSpace: "nowrap",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)", //   
            pointerEvents: "none",
            opacity: visible ? 1 : 0, // 
            transition: "opacity 0.2s ease", //   
        }}
        >
        {children}
        </div>,
        document.body
    );
}

function TooltipDesc({ name }){
    const descriptions = {
        'table_page': '',
        'dashboard': '',
        'aparts': ' /  '
    } 

    return descriptions[name]
}
